import Blogs from "@/Components/Blogs";
import createMetadata from "@/Components/Seo/createMetadata";
import { GetAddonsList } from "@/utilities/cookies";
import { notFound } from "next/navigation";
import { headers } from "next/headers";
import createStructuredData from "@/Components/Seo/createStructuredData";
import StructuredData from "@/Components/Seo/StructuredData";

export const dynamic = "force-dynamic";

export async function generateMetadata() {
  try {
    const h = await headers();
    const host = h.get("host");
    const proto = h.get("x-forwarded-proto") || "https";
    const base = `${proto}://${host}`;

    // Always return static metadata for the blogs list page
    return createMetadata({
      title: "Blogs – Latest Articles & Insights",
      description: "Explore our latest blogs, news, and articles.",
      keywords: "blogs, articles, news, updates",
      url: `${base}/blog`,
      baseUrl: base,
      showRobots: true,
    });
  } catch (error) {
    // Fallback metadata (NO IMAGE)
    const h = await headers();
    const host = h.get("host");
    const proto = h.get("x-forwarded-proto") || "https";
    const base = `${proto}://${host}`;

    return createMetadata({
      title: "Blogs",
      description: "Explore our latest blogs, news, and articles.",
      keywords: "blogs, articles, news",
      url: `${base}/blog`,
      baseUrl: base,
      showRobots: true,
    });
  }
}

export default async function page() {
  const addonList = await GetAddonsList();

  // Only 404 if we explicitly know the blog addon is disabled.
  if (addonList && addonList?.["blog"]?.status === 0) {
    notFound();
  }
  const headersList = await headers();
  const host = headersList.get("host");
  const protocol = headersList.get("x-forwarded-proto") || "https";
  const autoBaseUrl = `${protocol}://${host}`;
    // Create structured data
  const schema = createStructuredData({
    title: "Blogs – Latest Articles & Insights",
    description: "Explore our latest blogs, news, and articles.",
    url: `${autoBaseUrl}/blog`,
    organizationUrl: autoBaseUrl,
  });
  // If addonList or blog key is missing (e.g., no cookies on curl -I),
  // assume blogs are available and still render the page.
  return (<>
   <StructuredData data={schema} />
   <Blogs />
  </>);
}
